<template>
  <div class="form-holder">
    <form class="form" @submit.prevent="onSubmit" ref="form">
      <div class="form__group">
        <span class="form__input-title">*</span>
        <input class="form__input form__string-input"
               v-model.trim='$v.client.lastName.$model'
               placeholder=""
               :class="{ 'form--input-invalid': this.$v.client.lastName.$error }"
        >
        <span class="form__input-title">*</span>
        <input class="form__input form__string-input"
               id="firstName"
               v-model.trim='$v.client.firstName.$model'
               placeholder=""
               :class="{ 'form--input-invalid': this.$v.client.firstName.$error }"
        >
        <span class="form__input-title"></span>
        <input class="form__input form__string-input"
               v-model='client.patronymic'
               placeholder=""
        >
        <span class="form__input-title"> *</span>
        <input class="form__input form__date-input"
               type="date"
               v-model.trim='$v.client.birthDate.$model'
               :class="{ 'form--input-invalid': this.$v.client.birthDate.$error }"
        >
        <span class="form__input-title"> </span>
        <input class="form__input form__tel-input"
               type="tel"
               v-model.trim='$v.client.phoneNumber.$model'
               v-on:change="onChange()"
               :class="{ 'form--input-invalid': this.$v.client.phoneNumber.$error }"
        >
        <small
          class="form__invalid-message"
          v-if="!this.$v.client.phoneNumber.numeric"
        >
               
        </small>
        <small
          class="form__invalid-message"
          v-else-if="(!this.$v.client.phoneNumber.maxLength || !this.$v.client.phoneNumber.minLength)
          && this.$v.client.phoneNumber.$dirty"
        >
              11- ,   {{this.client.phoneNumber.length}}
        </small>
        <span class="form__input-title"></span>
        <div class="form__input">
          <input type="radio" value="" v-model="client.gender">
          <label></label>
          <input type="radio" value="" v-model="client.gender">
          <label></label>
        </div>
        <span class="form__input-title"> </span>
        <select class="form__input form__selector"
                multiple
                v-model="$v.client.category.$model"
                :class="{ 'form--input-invalid': this.$v.client.category.$error }"
        >
          <option class="form__option"></option>
          <option class="form__option">VIP</option>
          <option class="form__option"></option>
        </select>
        <span class="form__input-title"> </span>
        <select class="form__input form__selector" v-model="client.doctor">
          <option v-for="doctor in doctors" :key="doctor.name">
            {{ doctor.name }}
          </option>
        </select>
      </div>

      <div class="form__group">
        <span class="form__input-title"> </span>
        <input class="form__input form__number-input" v-model="client.postIndex" type="number" placeholder="270015">
        <span class="form__input-title"></span>
        <input class="form__input form__string-input" v-model="client.country" placeholder="">
        <span class="form__input-title"></span>
        <input class="form__input form__string-input" v-model="client.region" placeholder="">
        <span class="form__input-title">*</span>
        <input class="form__input form__string-input"
               placeholder=""
               v-model.trim='$v.client.city.$model'
               :class="{ 'form--input-invalid': this.$v.client.city.$error }"
        >
        <span class="form__input-title"></span>
        <input class="form__input form__string-input" v-model="client.street" placeholder="">
        <span class="form__input-title"></span>
        <input class="form__input form__string-input" v-model="client.house" placeholder="12-5">
      </div>

      <div class="form__group">
        <span class="form__input-title"> *</span>
        <select class="form__input form__selector"
                v-model="$v.client.documentType.$model"
                :class="{ 'form--input-invalid': this.$v.client.documentType.$error }"
        >
          <option class="form__option"></option>
          <option class="form__option"></option>
          <option class="form__option"></option>
        </select>
        <span class="form__input-title"> </span>
        <input class="form__input form__number-input" type="number" v-model="client.series" placeholder="BM">
        <span class="form__input-title"> </span>
        <input class="form__input form__number-input" type="number" v-model="client.number" placeholder="3848623">
        <span class="form__input-title"> </span>
        <input class="form__input form__string-input" v-model="client.issuedBy" placeholder=" ">
        <span class="form__input-title"> *</span>
        <input class="form__input form__date-input"
               type="date"
               v-model="$v.client.dateOfIssue.$model"
               placeholder=" "
               :class="{ 'form--input-invalid': this.$v.client.dateOfIssue.$error }"
        >
      </div>

      <div class="form__group">
        <div>
          <input class="form__input form__checkbox-input"
                 type="checkbox"
                 v-model="client.forbidMessage"
          >   
        </div>
        <span class="form__invalid-message" v-if="this.$v.$error">  (*) </span>
        <button v-if='!showSuccessMessage' type="submit" class="form__submit-button"></button>
      </div>
      <div v-if="showSuccessMessage" class="form__successMessage">
        <span class="form__successMessage-text"> </span>
      </div>
    </form>
  </div>
</template>

<script>
  import {minLength, maxLength, required, numeric} from 'vuelidate/lib/validators'

  export default {
    name: 'app',
    data() {
      return {
        doctors: [
          {name: '', specialisation: 'ophthalmologist'},
          {name: '', specialisation: 'cardiologist'},
          {name: '', specialisation: 'thanatologist'}
        ],
        showSuccessMessage: false,
        client: {
          lastName: '',
          firstName: '',
          patronymic: '',
          birthDate: '',
          phoneNumber: '7',
          gender: '',
          category: [],
          doctor: '',
          forbidMessage: false,

          postIndex: '',
          country: '',
          region: '',
          city: '',
          street: '',
          house: '',

          documentType: '',
          series: '',
          number: '',
          issuedBy: '',
          dateOfIssue: ''
        },
        clients: []
      }
    },
    validations: {
      client: {
        lastName: {required},
        firstName: {required},
        birthDate: {required},
        phoneNumber: {required, minLength: minLength(11), maxLength: maxLength(11), numeric},
        category: {required},
        city: {required},
        documentType: {required},
        dateOfIssue: {required}
      }
    },
    methods: {
      onSubmit(event) {
        if (this.$v.$invalid) {
          this.$v.$touch()
          return
        }
        this.clients.push(this.client)
        this.showSuccessMessage = true
        setTimeout(() => {
          this.showSuccessMessage = false
          event.target.reset()
        }, 2000)
      },
      onChange() {
        let newText = this.client.phoneNumber.split('');
        newText[0] = '7'
        this.client.phoneNumber = newText.join('')
      }
    }
  }
</script>

<style lang="sass">

    .form-holder
      max-width: 200px

    .form
      display: flex
      flex-direction: column

    .form__group
      display: flex
      flex-direction: column
      margin-bottom: 10px

    .form__input
      margin-bottom: 5px
      border: solid cornflowerblue 2px
      border-radius: 10px
      padding-left: 5px
      outline: 0

    .form__input-title
      color: dodgerblue

    .form__submit-button
      border-radius: 10px
      border: cornflowerblue solid 2px
      background-color: skyblue

    .form--input-invalid
      border: solid red 2px

    .form__invalid-message
      color: red

    .form__successMessage
      background-color: lightgreen
      border-radius: 10px
      padding: 10px
</style>



